
import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import normalizeCss from 'normalize.css';
import Popover from 'material-ui/Popover';
import Menu from 'material-ui/Menu';
import MenuItem from 'material-ui/MenuItem';
import IconButton from 'material-ui/IconButton';

import s from './Layout.css';
import Link from '../../components/Link';
import TitleImgUrl from '../../assets/images/title.png';
import LogoImgUrl from '../../assets/images/logo.png';

class Layout extends React.Component {
  static propTypes = {
    children: PropTypes.node.isRequired,
  };

  constructor(props) {
    super(props);
    this.state = {
      small: true,
      headerOpened: false,
      howitOpened: false,
    };
  }

  componentDidMount() {
    window.addEventListener('resize', this.updatedDimensions);
    this.updatedDimensions();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updatedDimensions);
  }

  updatedDimensions = () => {
    const small = window.innerWidth < 768;
    if (small !== this.state.small) {
      this.setState({
        small,
        headerOpened: false,
        howitOpened: false,
      });
    }
  }

  openHowIt = event => this.setState({
    howitOpened: true,
    anchorEl: event.currentTarget,
  });

  toggleHeader = () => this.setState({
    headerOpened: !this.state.headerOpened,
  });

  closeHeader = () => this.setState({
    headerOpened: false,
    howitOpened: false,
  });

  linkRender = (to, childs, className) => (
    <Link
      className={className !== undefined ? className : ''}
      to={to}
      onClick={this.closeHeader}
    >
      {childs}
    </Link>
  );

  render() {
    const { small, headerOpened } = this.state;
    const smallClass = small ? s.small : '';
    const openClass = headerOpened ? s.open : '';

    return (
      <div className={`${s.root} ${smallClass} ${openClass}`}>

        <header>
          <div className="container">
            <div className={s.headerBrandRoot}>
              { this.linkRender('/', <img src={TitleImgUrl} alt="" />, s.headerBrand) }
              <IconButton
                iconClassName="fa fa-bars"
                className={s.toggle}
                onTouchTap={this.toggleHeader}
              />
            </div>
            <ul>
              <li>
                <a onTouchTap={this.openHowIt}>
                  How it works <i className="fa fa-angle-down fa-lg" />
                </a>
              </li>
              <Popover
                className={`${s.subMenu} ${smallClass}`}
                open={this.state.howitOpened}
                anchorEl={this.state.anchorEl}
                onRequestClose={this.closeHeader}
              >
                <Menu>
                  <MenuItem>
                    { this.linkRender('/jobseeker', 'Job Seeker') }
                  </MenuItem>
                  <MenuItem>
                    { this.linkRender('/recruiter', 'Recruiter') }
                  </MenuItem>
                </Menu>
              </Popover>
              <li>
                { this.linkRender('/about', 'About') }
              </li>
              <li>
                { this.linkRender('/terms', 'Terms & Conditions') }
              </li>
              <li className={s.logout}>
                { this.linkRender('/login', 'Login') }
              </li>
            </ul>
          </div>
        </header>

        <div className={s.content}>
          {this.props.children}
        </div>

        <footer>
          <div className="container">
            <div className={s.footerBlock}>
              { this.linkRender('/', <h4><img src={LogoImgUrl} alt="" /></h4>) }
              <div>@ 2017 Sclabs Inc</div>
            </div>
            <div className={s.footerBlock}>
              <h4>MENU</h4>
              <div className={s.footerItem}>How it works</div>
              <Link
                className={s.footerItem}
                to="/jobseeker"
                onClick={this.closeHeader}
              >
                <i className="fa fa-genderless" />Job Seeker
              </Link>
              <Link
                className={s.footerItem}
                to="/recruiter"
                onClick={this.closeHeader}
              >
                <i className="fa fa-genderless" />Recruiter
              </Link>
              { this.linkRender('/about', 'About', s.footerItem) }
              { this.linkRender('/terms', 'Terms & Conditions', s.footerItem) }
              { this.linkRender('/login', 'Login', s.footerItem) }
            </div>
            <div className={s.footerBlock}>
              <h4>CONTACT</h4>
              <div className={s.footerItem}>
                <i className="fa fa-map-marker" />
                229 Leith Walk, Edinburgh EH6 8NY, UK
              </div>
              <a href="tel:+441315535900" className={s.footerItem}>
                <i className="fa fa-phone" />
                (131) 553-5900
              </a>
              <a href="mailto:mike@bodabar.com" className={s.footerItem}>
                <i className="fa fa-envelope" />
                mike@bodabar.com
              </a>
            </div>
            <div className={s.footerBlock}>
              <h4>FOLLOW US</h4>
              <a href="https://www.facebook.com/">
                <i className="fa fa-twitter fa-lg" />
              </a>
              <a href="https://twitter.com/">
                <i className="fa fa-facebook fa-lg" />
              </a>
              <a href="https://www.linkedin.com/">
                <i className="fa fa-linkedin fa-lg" />
              </a>
            </div>
          </div>
        </footer>

      </div>
    );
  }
}

export default withStyles(normalizeCss, s)(Layout);



